"use client";

import { forwardRef } from "react";
import ForceGraph2D from "react-force-graph-2d";

export const ForceGraphCanvas = forwardRef<any, Record<string, unknown>>(
  function ForceGraphCanvas(props, ref) {
  // react-force-graph types are permissive; keep this boundary typed loosely.
    return <ForceGraph2D ref={ref as any} {...(props as any)} />;
  }
);

